.gallery {
  grid-column: full-start / full-end;

  display: grid;
  //grid-template-columns: repeat(8, 1fr);
  //grid-template-rows: repeat(7, 5vw);
  grid-template: repeat(7, 5vw) / repeat(8, 1fr); //shorthand
  grid-gap: 1.5rem;
  padding: 1.5rem;

  @supports (display: grid) {
    display: grid;
    @include respond(phone) {
      display: grid;
    }
  }

  &__item {
    &--1 {
      grid-column: 1 / span 2;
      grid-row: 1 / span 2;
    }

    &--2 {
      grid-column: 6 / 7;
      grid-row: 1 / span 2;
    }

    &--3 {
      grid-column: 3 / span 3;
      grid-row: 1 / span 3;
    }

    &--4 {
      grid-column: 7 / span 2;
      grid-row: 1 / span 2;
    }

    &--5 {
      grid-column: 1 / span 2;
      grid-row: 3 / span 3;
    }

    &--6 {
      grid-column: 3 / span 2;
      grid-row: 4 / span 2;
    }

    &--7 {
      grid-column: 5 / 6;
      grid-row: 4 / span 1;
    }

    &--8 {
      grid-column: 6 / span 2;
      grid-row: 3 / span 2;
    }

    &--9 {
      grid-column: 8 / -1;
      grid-row: 3 / span 3;
    }

    &--10 {
      grid-column: 1 / 2;
      grid-row: 6 / span 2;
    }

    &--11 {
      grid-column: 2 / span 2;
      grid-row: 6 / span 2;
    }

    &--12 {
      grid-column: 4 / 5;
      grid-row: 6 / span 2;
    }

    &--13 {
      grid-column: 5 / span 3;
      grid-row: 5 / span 3;
    }

    &--14 {
      grid-column: 8 / -1;
      grid-row: 6 / span 2;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}
